/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f6f6f6;
  color: #747473;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.red {
  max-height: 100%;
  max-width: 100%;
  display: flex;
  justify-content: center;
}

h1, h2, h3 {
  font-family: "Open Sans", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #747473;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 2rem;
  text-align: center;
}

p {
  color: #747473;
  font-size: 1.8rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
  font-weight: 400;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: 2fr 1fr;
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  background-image: url(../img/Inicio.png);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}

.barra {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .boton {
  background-color: #008080;
  padding: 1rem 3rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  border-radius: 1rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem auto;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: #004d4d;
  cursor: pointer;
}

.red {
  margin-top: 2rem;
  text-align: center;
}

form {
  align-items: center;
}

.formulario {
  margin-top: 5rem;
  width: 100%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input,
.campo select {
  flex: 1;
  border: none;
  padding: 1rem;
  border-radius: 1rem;
}
.campo textarea {
  width: 100%;
  height: 20rem;
  border-radius: 1rem;
  border: none;
}
.campo__texto {
  margin-top: 3rem;
}
.campo__imagen {
  max-width: 40rem;
}

.boton {
  background-color: #00B2A4;
  padding: 1rem 3rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  border-radius: 1rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem auto;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #007f75;
  cursor: pointer;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  color: #008080;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}

.alerta {
  padding: 1.25rem;
  color: #ffffff;
  font-weight: 400;
  text-transform: uppercase;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  border-radius: 0.5rem;
}
.alerta.error {
  background-color: #b0d174;
}
.alerta.exito {
  background-color: #75c1a5;
}

.paginacion {
  padding: 1.6666666667rem 0;
}
@media (min-width: 480px) {
  .paginacion {
    display: flex;
  }
}
.paginacion .boton {
  margin: 0;
  padding: 1rem 4rem;
  background-color: #008080;
}

.contenedor-opciones {
  margin-top: 10rem;
}
@media (min-width: 768px) {
  .contenedor-opciones {
    display: flex;
    justify-content: space-around;
  }
}
.contenedor-opciones a {
  color: #01625e;
  font-weight: 700;
  font-size: 1.8rem;
  margin-bottom: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 8rem;
}
.contenedor-opciones p {
  margin-left: 1.8rem;
  color: #01625e;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__thead {
  background-color: #ffffff;
  color: #747473;
}
.table__th {
  padding: 1rem;
  text-align: left;
}
.table__tr:nth-of-type(even) {
  background-color: #e1e1e1;
}
.table__td {
  padding: 1.5rem 1rem;
}
.table__accion {
  all: unset;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
}
.table__accion--ver {
  color: #01625e;
}
.table__accion:hover {
  cursor: pointer;
}
.table__accion--ver {
  display: flex;
  justify-content: center;
  align-items: center;
}
.table .imagen-tabla {
  width: 35rem;
}