@use 'base/variables' as v;
@use 'base/mixins' as m;


.contenedor-app {
    @include m.tablet {
        display: grid;
        grid-template-columns: 2fr 1fr;
        min-height: 100vh;
    }

    .imagen {
        background-image: url(../img/Inicio.png);
        background-size: cover;
        background-position: center center;
        @include m.tablet{
            height: auto;
        }
    }

    .app {
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0 ;
    }
}

.barra{
    margin-bottom: v.$separacion;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
        
    }

    .boton{
        @include m.boton(v.$base);
        
    }
}

.red{
    margin-top: 2rem;
    text-align: center;
}

form{
    align-items: center;
}