@use 'base/variables' as v;
@use 'base/mixins' as m;


.contenedor-opciones {
    margin-top: calc(v.$separacion *2) ;
    

    
    @include m.tablet{
        display: flex;
        justify-content: space-around;
    }

    a{
        color: v.$verdeOscuro;
        font-weight: v.$bold;
        font-size: 1.8rem;
        margin-bottom: calc(v.$separacion / 2); 
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 8rem;

    }

    p {
        margin-left: 1.8rem;
        color: v.$verdeOscuro;
    }

}

