@use 'base/mixins' as m;
@use 'base/variables' as v;



.table{
    width: 100%;
    border-collapse: collapse;

    &__thead{
        background-color: v.$blanco;
        color: v.$gris;
    }

    &__th{
        padding: 1rem;
        text-align: left;
    }

    &__tr {
        &:nth-of-type(even){
            background-color: v.$grisClaro;
        }
    }

    &__td{
        padding: 1.5rem 1rem;

    }

    &__accion {
        all: unset;
        text-transform: uppercase;
        font-size: 1.4rem;
        font-weight: v.$bold;
        &--ver {
            color: v.$verdeOscuro;
        }
        &:hover {
            cursor: pointer;
        }
    }

   &__accion--ver{
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .imagen-tabla {
        width: 35rem;
    }
}