@use 'base/variables' as v;
@use 'base/mixins'  as m;

.formulario {
    margin-top: 5rem;
    width: 100%;
}

.campo {
    display: flex;
    margin-bottom: 2rem;
    align-items: center;
    label {
        flex: 0 0 10rem;
    }

    input,
    select{
        flex: 1;
        border: none;
        padding: 1rem;
        border-radius: 1rem;
    }

    textarea{
        width: 100%;
        height: 20rem;
        border-radius: 1rem;
        border: none;

    }

    &__texto{
        margin-top: 3rem ;
    }

    &__imagen{
        max-width: 40rem;
    }
}