@use 'base/variables' as v;
@use 'base/mixins' as m;

.alerta {
    padding: calc(v.$separacion/4);
    color: v.$blanco;
    font-weight: v.$regular;
    text-transform: uppercase;
    width: 100%;
    display: block;
    margin-bottom: 2rem;
    text-align: center;
    border-radius: .5rem;


    &.error {
        background-color: v.$verde;
    }

    &.exito {
        background-color:v.$verdeClaro;
    }
}